.gamesAndSort {
  width: 100%;
  display: flex;
  color: #ffffff;
  align-items: center;
  justify-content: space-between;
  padding: 40px;
  position: relative;
  gap: 16px

}
.gamesText {
  font-size: 24px;
  font-weight: bold;
}
.sortByText {
  font-size: 20px;
}
.sortSelect {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
  margin-right: 32px;
}
.gamesSelect {
  border: none;
  background-color: #3a3e45;
  outline: none;
  border-radius: 4px;
  padding: 10px;
  color: white;
  font-size: 14px;
  width: 250px;
}

.valueSelect {
  text-decoration: none;
  box-shadow: 0 0 10px 100px #fff inset;
}
.dropdown-container {
  position: relative;
  width: 300px;
  background-color: #2b2b2b;
  border: none;
  border-radius: 4px 4px 0 0;
  font-size: 15px;
  padding: 4px 8px 4px 8px;
}

.selected-option {
  cursor: pointer;
  padding: 8px;
  display: flex;
  align-items: center;
  gap: 4px;
  border-radius: 4px;
  user-select: none;
}

.options-list {
  list-style: none;
  padding: 8px;
  margin: 0;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background-color: #2b2b2b;
  overflow: auto;
  border-top: none;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}
.options-list::-webkit-scrollbar {
  width: 8px;
}
.options-list::-webkit-scrollbar-track {
  background-color: #3a3a3a;
  border-radius: 8px;
}
.options-list::-webkit-scrollbar-thumb {
  background: #fe4300;
  border-radius: 8px;
}

.option {
  padding: 10px;
  cursor: pointer;
  transition: background-color 0.3s;
  user-select: none;
}

.option:hover {
  background-color: #3a3a3a;
  border-radius: 4px;
}
.oragneElement {
  width: 4px;
  height: 12px;
  background-color: #fe4300;
  border-radius: 4px;
}
.arrow {
  position: absolute;
  right: 24px;
  top: 4px;
  transform: translateY(50%);
}
.filtersGames {
  width: 100%;
  display: flex;
  padding: 0 40px 0 40px;

}
.filters {
  width: 20%;
  color: white;
  font-size: 16px;
}
.games {
  width: 80%;
}
.hr {
  width: 300px;
  height: 1.2px;
  background-color: #808080;
  margin: 24px 0;
}
.genres {
  font-size: 22px;
  margin-bottom: 16px;
}
.containerGamesStore {
  display: flex;
  flex-wrap: wrap;
  gap: 20px 75px;
}
.searchStore {
  height: 40px;
  width: 400px;
  background-color: #24262a;
  margin-right: 50px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 4px;
  z-index: 15;
}
.searchIconStore {
  width: 40px;
  height: 32px;
  background-color: transparent;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 4px;
}
.input::placeholder {
  font-weight: 600;
}
.gameNotFound {
  color: #fafafa;
  font-size: 24px;
}

@media (max-width: 768px){
  .gamesText {
   display: none;
  }
  .filtersGames {
    align-items: center;
    flex-direction: column;
    padding: 0;
  }
  .filters {
    padding:0 24px;
    flex-direction: column;
    margin-bottom: 40px;
  }
  .gamesAndSort {
    padding: 0 24px;
    position: static;
    flex-direction: column;
    margin-top: 16px;
  }
  .searchStore {
    width: 300px;
    margin-right: 0;
    position: static;
  }
  .sortSelect {
    align-items: flex-start;
    flex-direction: column;
    margin-top: 16px;
    margin-right: 0;
    padding: 0;
  }
  .containerGamesStore {
    justify-content: center;
    gap: 0;
  }
  .genres {
    align-self: flex-start;
  }
  .filters {
    width: auto;
    display: flex;
    align-items: flex-start;
    margin-top: 24px;
  }

}
@media (min-width: 768px) and (max-width: 992px){
  .gamesText {
    display: none;
  }
  .filtersGames {
    align-items: center;
    flex-direction: column;
    padding: 0;
  }
  .filters {
    padding:0 24px;
    flex-direction: column;
    margin-bottom: 40px;
  }
  .gamesAndSort {
    padding: 0 24px;
    position: static;
    flex-direction: column;
    margin-top: 16px;
  }
  .searchStore {
    width: 300px;
    margin-right: 0;
    position: static;
  }
  .sortSelect {
    align-items: flex-start;
    flex-direction: column;
    margin-top: 16px;
    margin-right: 0;
    padding: 0;
  }
  .containerGamesStore {
    justify-content: center;
    gap: 16px;
  }
  .genres {
    align-self: flex-start;
  }
  .filters {
    width: auto;
    display: flex;
    align-items: flex-start;
    margin-top: 24px;
  }
}